import urls from '../../utils/urls.js'
export default {
	data() {
		return {
			isAgree: true,
			isSure: false,
			second: 60,
			showText: true,
			phone: '',
			showLoading: false,
			cid:''  //个推id
		}
	},
	onLoad() {
		uni.hideLoading()
		this.cid = plus.push.getClientInfo().clientid
	},

	methods: {
		checkAgree: function(e) {
			if (e.detail.value.length > 0) {
				this.isAgree = true
			} else {
				this.isAgree = false
			}
		},
		getPhone: function(e) {
			this.phone = e.detail.value
		},
		getIdenCode: function(e) {
			if (e.detail.value.length >= 4) {
				this.isSure = true
				this.showText = true
				this.second = 60
			} else {
				uni.showToast({
					title: '请填写正确的验证码',
					icon: 'none'
				})
			}
		},
		getCode: function() {
			var reg = /^1[345678]\d{9}$/
			var phone = this.phone
			if (!reg.test(phone)) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none'
				})
			} else {
				this.showText = false
				var interval = setInterval(() => {
					--this.second
				}, 1000)
				uni.request({
					url: urls.HTTP + '/user/getmcodenum',
					method: 'POST',
					data: {
						username: phone
					},
					success: (res) => {
						if (res.data.code === 0) {
							uni.showToast({
								title: res.data.error,
								icon: 'none'
							})
						} else {								
							uni.showToast({
								title: res.data.error,
								icon: 'none'
							})
						}
					}
				})
				setTimeout(() => {
					clearInterval(interval)
					this.second = 60
					this.showText = true
				}, 60000)
			}
		},
		more: function() {
			uni.switchTab({
				url: '../find/find'
			})
		},
		submit: function(e) {
			if (this.isAgree) {
				if (!e.detail.value.phone || !e.detail.value.code) {
					uni.showToast({
						title: '手机号或验证码不能为空',
						icon: 'none'
					})
				} else {
					this.showLoading = true
					uni.request({
						url: urls.HTTP + '/user/checkpost',
						method: 'POST',
						data: {
							mobile: e.detail.value.phone,
							code: e.detail.value.code,
							cid : this.cid
						},
						success: (res) => {
							if (res.data.code === 0) {
								this.showLoading = false
								uni.setStorageSync('user', res.data.user)
								uni.setStorage({
									key: 'userInfo',
									data: JSON.stringify(res.data.user), // 对象转字符串进行本地存储
									success: (res) => {
										console.log(res)
									}
								})
								// console.log(uni.getStorageSync('user'))
								// this.$store.dispatch('userInfo', res.data.user)
								this.$store.dispatch('isLogin', true)
								if (res.data.user.sex !== 0) {
									uni.switchTab({
										url: '../find/find'
									})
								} else {
									uni.redirectTo({
										url: 'chooseSex'
									})
								}
							} else {
								uni.showToast({
									title: res.data.error,
									icon: 'none'
								})
							}
						}
					})
				}
			} else {
				uni.showToast({
					title: '请先阅读协议并同意',
					icon: 'none'
				})
			}
		},
		wxLogin: function() {

			uni.showLoading({
				title: '正在登录...',
				mask: true
			})
			uni.login({
				provider: 'weixin',
				success: (res) => {
					uni.getUserInfo({
						provider: 'weixin',
						success: (infoRes) => {
							//Console.log(infoRes.userInfo);
							uni.setStorageSync('openid', infoRes.userInfo.unionId)
							uni.request({
								url: urls.HTTP + '/callback/appwxlogin',
								method: 'POST',
								data: {
									country: infoRes.userInfo.country,
									nickname: infoRes.userInfo.nickName,
									gender: infoRes.userInfo.gender,
									province: infoRes.userInfo.province,
									city: infoRes.userInfo.city,
									headimgurl: infoRes.userInfo.avatarUrl,
									unionid: infoRes.userInfo.unionId
								},
								success: (res) => {										
									uni.hideLoading()
									if (res.data.code === 0) {
										this.$store.dispatch('userInfo', res.data.user)
										uni.setStorageSync('user', res.data.user)
										uni.setStorage({
											key: 'userInfo',
											data: JSON.stringify(res.data.user), // 对象转字符串进行本地存储
											success: (res) => {
												console.log(res)
											}
										})
										this.$store.dispatch('isLogin', true)
										if (res.data.user.sex !== 0) {
											uni.switchTab({
												url: '../find/find'
											})
										} else {
											uni.redirectTo({
												url: 'chooseSex'
											})
										}
									} else if (res.data.code === 1) {
										this.$store.dispatch('loginType', 1)
										uni.redirectTo({
											url: 'register'
										})
									}
								}
							})
						}
					})
				}
			})
		},
		qqLogin: function() {
			uni.login({
				provider: 'qq',
				success: (loginRes) => {
					uni.getUserInfo({
						provider: 'qq',
						success: (infoRes) => {
							uni.setStorageSync('qqopenid', infoRes.userInfo.openId)
							uni.request({
								url: urls.HTTP + '/callback/appqqlogin',
								method: 'POST',
								data: {
									nickname: infoRes.userInfo.nickName,
									gender: infoRes.userInfo.gender,
									province: infoRes.userInfo.province,
									city: infoRes.userInfo.city,
									year: infoRes.userInfo.year,
									headimgurl: infoRes.userInfo.headimgurl,
									openid: infoRes.userInfo.openId
								},
								success: (res) => {
									// console.log('登录', res)
									if (res.data.code === 0) {
										this.$store.dispatch('userInfo', res.data.user)
										uni.setStorageSync('user', res.data.user)
										uni.setStorage({
											key: 'userInfo',
											data: JSON.stringify(res.data.user), // 对象转字符串进行本地存储
											success: (res) => {
												console.log(res)
											}
										})
										// console.log(uni.getStorageSync('user'))
										// this.$store.dispatch('userInfo', res.data.user)
										this.$store.dispatch('isLogin', true)
										if (res.data.user.sex !== 0) {
											uni.switchTab({
												url: '../find/find'
											})
										} else {
											uni.redirectTo({
												url: 'chooseSex'
											})
										}
									} else if (res.data.code === 1) {
										this.$store.dispatch('loginType', 2)
										uni.redirectTo({
											url: 'register'
										})
									}
								}
							})
						}
					})
				}
			})
		}
	}
}
